@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: row;
          flex-direction: row;
 
}
.main {
    margin: 0;
    padding: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    background: url(/static/media/bg2.54566fca.jpg) center;
    background-size: cover;
    height : 100vh;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
}

.navbar {
    overflow: hidden;
    background-color: transparent;
    position: fixed; 
    width: 20%;
    height: 100vh; 
    -webkit-flex: 1 1; 
            flex: 1 1;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
}
@font-face {
    font-family: 'Lazer84';
    font-style: normal;
    src: url(/static/media/Lazer84.b12d7dca.ttf);
}

div.container
{
    font-family: 'Lazer84';
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

div.container a
{
    text-decoration: none;
    font: 20px "Lazer84";
    margin: 0px 10px;
    padding: 10px 10px;
    position: relative;
    z-index: 0;
    cursor: pointer;
    color: white;
}

div.highlightTextOut a:before, div.highlightTextIn a:before
{
    position: absolute;
    color: rgb(236, 106, 219);
    overflow: hidden;
    content: attr(alt);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    opacity: 0;
} 

div.highlightTextOut a:hover:before, div.highlightTextIn a:hover:before
{
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}

.img{
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
    width: 30vh;
}

.img:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.icon{
    margin: 1vh
}
